import React from 'react'
import { logOut } from '../actions'
import { Link } from 'react-router-dom'
const Header = (props) => (
	<div className="header">
		<Link to='/'>Home</Link>
		<button className='logout-button' onClick={function(){props.dispatch(logOut())}}>
			LogOut
		</button>
	</div>
)
export default Header