var initialState={
	isAuthorized:false,
	name:'',
	sessionId:0,
	menu:[]
}
//const initialState='asd'

export default function authorization(state = initialState, action) {
	switch(action.type){
		case 'SEND_AUTHORIZATION' :
			return {
				...state,
				name:action.login,
				isAuthorized:false
			}
		case 'LOGOUT':
			return {
				isAuthorized:false,
				name:''
			}
		case 'RECEIVE_POSTS':
			return {
				...state,
				isAuthorized:action.data.id!==undefined,
				name:action.data.last_name,
				menu:action.data.userMenuItems,
				sessionId:action.data.session_id
			}
		default :
			return state
	}
}