import React,{Component} from 'react'
import PropTypes from 'prop-types'
import { sendAuth, logOut } from '../actions'
class LoginForm extends Component{
    constructor(props){
        super(props)
        this.state={
            login:'admin',
            password:'admin900'
        }
    }
    changeLogin = (e) =>{
        this.setState({
            login:e.target.value
        })
    }
    changePassword = (e) =>{
        this.setState({
            password:e.target.value
        })
    }
    logOut=()=>{
        this.props.dispatch(logOut())
    }
    onClick=()=>{
        this.props.dispatch(sendAuth(this.state.login,this.state.password))
        this.setState({
            login:'',
            password:''
        })
    }
    render(){
        const {isAuthorized, name} = this.props
        var input
        const form= isAuthorized ? (
            <div>
                Hello {name}
                <button onClick={this.logOut}>Logout</button>
            </div>
            ) : (
        <div className='login-form'>
            <table>
                <tbody>
                    <tr>
                        
                        <td>{name}</td>
                    </tr>
                    <tr>
                        <td>Login</td>
                        <td><input value={this.state.login} onChange={this.changeLogin} /></td>
                    </tr>
                    <tr>
                        <td>Password</td>
                        <td><input type="password" value={this.state.password} onChange={this.changePassword} /></td>
                    </tr>
                    <tr>
                        <td></td>
                        <td>
                            <button onClick={this.onClick}>
                                Login
                            </button>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>
        )
        return (
            <div>
                {form}
            </div>
        )
    }
}
LoginForm.propTypes = {
    isAuthorized:PropTypes.bool,
    name:PropTypes.string,
    dispatch:PropTypes.func.isRequired

}
export default LoginForm