var initialState={
	userList:[],
	isRequest:false
}


export default function users(state = initialState, action) {
	switch(action.type){
		case 'RECEIVE_USERS_REQUEST' :
			return {
				...state,
				isRequest:true
			}
		case 'RECEIVE_USERS':
			return {
				...state,
				userList:action.data
			}
		default :
			return state
	}
}